@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;700&display=swap);
body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root{
    --white:#FFFF;
    --PrimaryColor: #009e61;
    --gray: #eeeeee;
    --black: #252525;
    --lightBlack: #353535;
    --red: #c63637;
    --green: #42ab49;
}

body{
    background: #eeeeee;
    background: var(--gray);
}

*{
    margin:0;
    padding:0;
    box-sizing: border-box;
}
.nav{
    width: 100%;
    height: 80px;
    background: #252525;
    display:flex;
    align-items: center;
    justify-content:space-around;
}

.nav__img{
    height: 60px;
    object-fit: cover;
}
.nav__user{
    display:flex;
    align-items: center;
    color:white;
    grid-gap:5px;
    gap:5px;
    cursor:pointer;
    border: 1px solid rgb(95, 95, 95);
    padding: 2px 6px;
}

.nav__user:hover{
    background: var(--lightBlack);
}

.nav__user:hover > .fa-sign-out-alt{
    color:red;
    transition: all 0.5s ease;
}

.nav__user-foto{
    height: 40px;
    object-fit: contain;
    margin-right: 5px;
}

.nav__user-name{
    font-size: 1.1rem;
}

.fa-sign-out-alt{
    margin-top:1px;
    margin-left: 2px;
}
.login-page{
    min-height: calc(100vh - 80px - 80px);
    display:flex;
    justify-content: center;
    align-items: center;
}

.login-container{
    display:flex;
    flex-direction: column;
    width: 30rem;
    height: 25rem;
    box-shadow: 0px 0px 7px 5px rgb(0 0 0 / 10%);
    border-radius: 15px;
    grid-gap:20px;
    gap:20px;
    justify-content: center;
    align-items: center;
    background: var(--white);
}

.login-container__img{
    height: 150px;
    object-fit: contain;
}

.login-container__input{
    width: 70%;
    line-height: 2;
    text-align: center;
}

.login-container__btn{
    padding:12px 35px;
    cursor:pointer;
    background: var(--black);
    color: var(--white);
    border:none;
    border-radius: 5px;
}

.login-container__btn:hover{
    background:var(--lightBlack);
    transform: scale(1.1);
    transition: all 0.3s ease;
}

@media (max-width: 500px){
    .login-container{
        width: 80%;
        height: 20rem;
    }

    .login-page{
        min-height: calc(100vh - 80px - 90px);
    }
}
.main-barra{
    display:flex;
    align-items: center;
    justify-content: space-around;
    background: var(--white);
    width: 75%;
    padding: 10px 0;
    box-shadow: 0px 0px 7px 5px rgb(0 0 0 / 10%);
}

.main-barra__turno{
    display:flex;
    grid-gap:5px;
    gap:5px;
}

.main-barra__btn{
    height: 30px;
    width: 30px;
    border-radius: 50%;
    border: 1px solid grey;
    cursor:pointer;
}

.main-barra__btn:hover{
    transform: scale(1.1);
    transition: all 0.3s ease-in-out;
}

@media (max-width: 700px){
    .main-barra{
        flex-direction: column-reverse;
        grid-gap:10px;
        gap:10px;
        width: -webkit-fit-content;
        width: -moz-fit-content;
        width: fit-content;
    }

    .main-barra__turno{
        margin: 0 10px;
    }
}
.motociclistas-container{
    display:grid;
    grid-template-columns: auto auto auto auto;
    width: 70%;
    grid-gap: 20px;
    gap: 20px;
    margin-bottom:40px;
}

@media (max-width: 900px){
    .motociclistas-container{
        grid-template-columns: auto auto auto;
        width: 80%;
    }
}

@media (max-width: 500px){
    .motociclistas-container{
        grid-template-columns: auto auto;
        width: 80%;
    }
}
.motociclista{
    background: var(--green);
    height: 120px;
    text-align: center;
    display:flex;
    flex-direction: column;
    justify-content: center;
    color:white;
    box-shadow: 0px 0px 7px 5px rgb(0 0 0 / 10%);
    cursor:pointer;
}

.motociclista h4{
    font-size: 1.2rem;
}

.ocupado{
    background: var(--red);
}

.main-motos{
    display:flex;
    flex-direction: column;
    align-items: center;
    grid-gap:20px;
    gap:20px;
    margin-top: 30px;
    min-height: calc(100vh - 80px - 80px - 30px);
}

.main-motos__texto{
    text-align: center;
    margin-bottom:10px;
}

.main-motos__texto h1{
    margin-bottom: 5px;
}

.main-motos__texto p{
    font-size: 1.1rem;
    margin: 0 20px;
}
footer{
    display:flex;
    justify-content: space-around;
    background: var(--black);
    color:white;
    height: 80px;
    align-items: center;
}

footer p{
    margin-bottom:10px;
}

.footer__rrss{
    display:flex;
    margin-top:10px;
    grid-gap:10px;
    gap:10px;
}

.footer__rrss a{
    color:white;
    text-align: center;
    text-decoration: none;
}

.footer__rrss a i{
    font-size: 2rem;
}

.footer__link:hover{
    transform: scale(1.1);
    transition: all 0.3s ease;
}

@media (max-width: 500px){
    footer{
        flex-direction: column-reverse;
        height: 90px;
    }
}
